  fill: #eeeeee;
}
.feature.green svg .triangle {
  fill: #278b27;
}
.feature.blue svg .triangle {
  fill: #a5b2c7;
}
.feature.lightgreen svg .triangle {
  fill: #d0de33;
}
.feature.purple svg .triangle {
  fill: #9a97b8;
}
.feature.black svg .triangle {
  fill: #212121;
}
.feature.white svg .triangle {
  fill: #ffffff;
}
